<template>
  <div class="header_navs">
    <div
      class="nav_item"
      v-for="(item, index) in tabbars"
      :key="index"
      :class="activeIndex == index ? 'active' : ''"
      @click="turn(item.name, index)"
    >
      <i class="nav" :class="item.navclass"></i>
      <span>{{ item.title }}</span>
    </div>
    <!-- <span class="suoxiao_class icon">
      <i class="iconfont icon-suoxiao1" @click="close"></i>
    </span>
    <span class="scale_class icon">
      <i :class="'iconfont ' + icona" @click="scale(icona)"></i>
    </span>
    <span class="close_class icon" @click="close">
      <i class="el-icon-close"></i>
    </span>-->
    <!-- <span><i class="iconfont icon-fangda"></i></span> -->
    <!-- <span><i class="iconfont icon-fangda"></i></span> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      icona: "icon-fangda",
      activeIndex: "1",
      tabbars: [
        {
          name: "conventional",
          title: "常规检查",
          navclass: "nav1",
          normal: "" //图标
        },
        {
          name: "fileCheck",
          title: "文件检查",
          navclass: "nav2",

          normal: "" //图标
        },
        {
          name: "emailCheck",
          title: "邮件检查",
          navclass: "nav3",

          normal: "" //图标
        },
        {
          name: "depthCheck",
          title: "深度检查",
          navclass: "nav4",

          normal: "" //图标
        },
        {
          name: "information",
          title: "信息维护",
          navclass: "nav5",

          normal: "" //图标
        },
        {
          name: "report",
          title: "本机报告",
          navclass: "nav6",

          normal: "" //图标
        }
      ]
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    turn(name, index) {
      console.log(name, index);
      this.activeIndex = index;
      this.$router.push({ name: name });
    },
    // 取消弹窗
    close() {
      this.$emit("closeDialog", false);
    },
    // 放大缩小按钮
    scale(icon) {
      if (icon == "icon-fangda") {
        this.$emit("scale", "big");
        this.icona = "icon-suoxiao";
      } else if (icon == "icon-suoxiao") {
        this.$emit("scale", "small");
        this.icona = "icon-fangda";
      }
    }
  },
  computed: {}
};
</script>

<style lang="less" scoped>
.header_navs {
  width: 100%;
  height: 90px;
  background: url(../../public/images/navbg.jpg) repeat;
  position: relative;

  .nav {
    display: block;
    width: 48px;
    height: 48px;
    // border: 1px solid;
  }
  .nav {
    margin-left: 13px;
    margin-top: 4px;
    background-image: url(../../public/images/logo_module.png);
    //   background-size: 40px 40px;
  }
  .nav1 {
    background-position: -45px, 0;
  }
  .nav2 {
    background-position: -141px, 0;
  }
  .nav3 {
    background-position: -189px, 0;
  }
  .nav4 {
    background-position: -243px, 0;
  }
  .nav5 {
    background-position: -430px, 0;
  }
  .nav6 {
    background-position: -478px, 0;
  }
  .nav_item {
    width: 80px;
    height: 80px;
    margin: auto;
    float: left;
    margin: 10px 5px 0;
    box-sizing: border-box;
    // border: 1px solid;
    text-align: center;
    span {
      display: inline-block;
      line-height: 28px;
      color: #fff;
    }
  }
  .icon {
    position: absolute;
    top: 6px;
    color: white;
    font-size: 16px;
    display: inline-block;
    width: 22px;
    height: 22px;
    text-align: center;
    line-height: 22px;
  }
  .icon:hover {
    background: #88c3f5;
  }
  .suoxiao_class {
    right: 84px;
  }
  .scale_class {
    right: 50px;
  }
  .close_class {
    right: 16px;
  }
  .nav_item:hover {
    cursor: pointer;
    background-image: url(../../public/images/hover_bg1.png);
    border-radius: 10px;
    background-size: 80px 80px;
  }
  .active {
    span {
      color: #000;
    }
    background-image: url(../../public/images/hover_bg1.png);
    border-radius: 10px;
    background-size: 80px 80px;
  }
}
</style>
