import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    leftCol: 5,
    rightCol: 19,
    tabStyle: "small_class",
  },
  mutations: {
    changeColNum(state, num) {
      console.log(num);
      state.leftCol = num;
      state.rightCol = 24 - num;
    },
    changeClass(state, type) {
      state.tabStyle = type;
    },
  },
  actions: {},
  modules: {},
});
